.card {
	display: block;
	/*border: 2px solid #1Eb980;*/
	/*background-color: #045D64;*/
	border-radius: 3px;
	/*padding: 2.5%;*/
	/*margin: 0.5em 0;*/
	padding: 1.5% 0 1.5% 0;

}

.card-container {
	max-width: 40em;
	-webkit-transition: 0.5s ease all;
	-o-transition: 0.5s ease all;
	transition: 0.5s ease all;
}

.card-info {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: stretch;
	    align-items: stretch;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;

}

.card-name, .card-img {
	display: inline-block;
	vertical-align: middle;

}

.card-img {
	max-width: 54px;
	max-height: 54px;
	/*border: 1px solid whitesmoke;*/
	/*border-radius: 32px;*/
	/*background-color:whitesmoke;*/
}

.card-img img {
	border-radius: 50%;
}

.card-employee {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
}

.contain {
	height: 100%;
	width: 100%;
	-o-object-fit: contain;
	   object-fit: contain;
}

.card-name, .card-frame, .card-time-diff {
	font-size: 12px;
	color: #edeef0;
}

.opt-btn {
	border: none;
	background: transparent;
	color: whitesmoke;
	font-size: 18px;
	padding: 0;
}

.opt-btn:hover,
.opt-btn:focus {
	text-shadow: 0 0 1px #8aff37,
	0 0 10px rgba(64, 199, 57, 0.8);
}

.card-opt-panel {
	margin-top: 0.5em;
	display: none;
}

.card-opt-panel.is-open {
	display: block;
}

.card-opt-panel-body {
	background-color: rgb(8, 67, 71);
	border-radius: 3px;
}

.card-opt {
	color: whitesmoke;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	padding: 1em 0;
	margin: 0 auto;
	height: 3em;
}

.card-date {
	background-color: #2b2d3d;
	margin: 1.5% 0 1.5% 0;
	text-align: left;
	color: #9fa0a4;
	font-size: 12px;
}

.card-time-diff {
	color: #edeef0;
	border-radius: 50%;

	border-style: solid;
	border-width: 1px;
	border-top-color: #35df5c;
	border-right-color: #288b3f;
	border-bottom-color: #1c7932;
	border-left-color: #6bc580;
	padding: 18px 5px 18px 5px;
	width: 54px;

}

.card-time {
	max-width: 54px;
	max-height: 54px;
}


