@-webkit-keyframes fadein {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes fadein {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

.form-wrapper {
    margin: 0 auto;
    padding: 20px;
    background-color: #232633;
    -webkit-animation: fadein 0.8s;
            animation: fadein 0.8s;
}

.form-wrapper h2 {
    text-align: center;
}

.form-field {
    position: relative;
    margin-bottom: 25px;
}

.form-field label {
	display: block;
	text-align: left;
    text-transform: uppercase;
    padding-top: 1.5rem;
    color: #8e909a;
    font-size: 11px;
}

.form-field input,
.form-field select {
    display: block;
    width: 100%;
    margin-top: 5px;
	line-height: 24px;
    /*font-size: calc(.5rem + .9vw);*/
    font-size: 12px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background-color: #32364a;
    color: #FFFFFF;
    border: 1px solid #32364a;
    padding-left: 2.5%;
}

.form-field select {
    padding: 5px;
}

.form-submit-btn {
	border-radius: 50px;
    background: transparent;
    padding: 10px 50px;
    border: 2px solid #35df5c;
    color: #35df5c;
    text-transform: uppercase;
	font-size: 12px;
	-webkit-transition-property: background,color;
	-o-transition-property: background,color;
	transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
    letter-spacing: 1px;
}

.form-submit-btn:hover,
.form-submit-btn:focus {
    color: black;
    background: #35df5c;
    cursor: pointer;
    -webkit-transition-property: background,color;
    -o-transition-property: background,color;
    transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
}

.form-error {
    color: #ce3d3d;
    font-size: 11px;
    position: absolute;
    bottom: -1.5rem;
    text-transform: uppercase;
}

.error-border {
    border: 1px solid #ce3d3d !important;
}

.form-delete-btn {
    border-radius: 50px;
    background: transparent;
    padding: 5px 10px;
    border: 2px solid #ce3d3d;
    color: #ce3d3d;
	font-size: 28px;
	-webkit-transition-property: background,color;
	-o-transition-property: background,color;
	transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
    letter-spacing: 1px;
    margin: 0 10px;
}

.form-delete-btn:hover,
.form-delete-btn:focus {
    color: black;
    background: #ce3d3d;
    cursor: pointer;
    -webkit-transition-property: background,color;
    -o-transition-property: background,color;
    transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
}

.form-reset-btn {
    background: transparent;
    padding: 0;
    /*margin-bottom: 10px;*/
    border: none;
    border-bottom: 2px solid #35cbdf;
    color: #35cbdf;
    text-transform: uppercase;
	font-size: 12px;
	-webkit-transition-property: background,color;
	-o-transition-property: background,color;
	transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
    letter-spacing: 1px;
}

.form-reset-btn:hover,
.form-reset-btn:focus {
    color: black;
    background: #35cbdf;
    cursor: pointer;
    -webkit-transition-property: background,color;
    -o-transition-property: background,color;
    transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
}

.form-btns {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

/* PROFILE FORM */

.profile-form-field {
    text-align: center;
    padding-top: 25px;
}

.profile-save-btn:hover,
.profile-save-btn:focus {
    color: black;
    background: #35df5c;
    cursor: pointer;
    -webkit-transition-property: background,color;
    -o-transition-property: background,color;
    transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
            transition-duration: .2s;
}

/* LOGIN FORM */

.login-form-wrapper {
    border-left: 2px solid #35df5c;
    margin: 0 auto;
    padding: 20px;
    max-width: 360px;
}

.form-header {
    color: white;
}

