.filter-form-submit-btn:focus {
    color: black;
    background: #35df5c;
    cursor: pointer;
    -webkit-transition-property: background,color;
    -o-transition-property: background,color;
    transition-property: background,color;
    -webkit-transition-duration: .2s;
         -o-transition-duration: .2s;
